.home {
    width: 100%;
    height: 100vh;
} 

.home .container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(rgba(32,32,32,.7), rgba(32,32,32,.7)), url('../../images/francishui.jpg') no-repeat center center/cover;
}

.home .top {
    margin-top: 7rem;
    color: #f6f6f6;
}
.home .bottom {
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 600px;
    margin-bottom: 4rem;
}

@media screen and (max-width:540px) {
    .home .container .bottom {
        flex-direction: column;
        align-items: center;
    }
}